body {
    min-width: 1024px;
    overflow: hidden;
    margin: 0px;
    padding: 0px;
    background: #000;
    font-family: sans-serif, "Helvetica Neue";
}   
canvas {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%; 
    height: 100%;
    z-index: -1;
}

.title {
    height: 100vh;
    font-size: 390px;
    font-stretch: ultra-condensed;
    font-weight: 800;
    letter-spacing: -20px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -50px;
}

::-webkit-scrollbar {
    width: 2px;
}
::-webkit-scrollbar-track {
    border-radius: 2px;
    background: #333;
}
::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background: #999; 
}
::-webkit-scrollbar-thumb:window-inactive {
    background: #999; 
}



